body{
    font-family: Arial, Helvetica, sans-serif;
    height: 100vh;
    background: linear-gradient(rgb(99, 140, 143),rgb(106, 250, 226));
}
.container{
    display: flex;
    flex-direction: column;
    margin: auto;
    margin-top: 50px;
    border-radius: 30px;
    border: 2px solid rgb(9, 53, 90);
    width: 500px;
    background: #fff;
    padding-bottom: 30px;
}
.header{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 9px;
    width: 100%;
    margin-top: 30px;
}
.text{
    color: rgb(54, 80, 48);
    font-size: 50px;
    font-weight: 500;
}
.underline{
    width: 60px;
    height: 6px;
    background: rgb(105, 91, 73);
    border-radius: 5px;
}
.inputs{
    margin-top: 60px;
    display: flex;
    flex-direction: column;
    gap: 26px;

}
.input{
    display: flex;
    align-items: center;
    margin: auto;
    width: 400px;
    height: 40px;
    background: #eaeaea;
    border-radius: 8px;
    padding: 10px;
}
.input img{
    margin: 0px 30px;
}
.input input{
    height: 50px;
    width: 400px;
    background: transparent;
    border: none;
    outline: none;
    color: #797979;
    font-size: 20px;
}
.forgotPassword{
    padding-left: 60px;
    margin-top: 30px;
    color: #797979;
    font-size: 20px;
}
.forgotPassword span{
    color: #4c00b4;
    cursor: pointer;
}
.submitContainer{
    display: flex;
    gap: 30px;
    margin: 10px auto;
}
.submit{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 200px;
    height: 50px;
    border-radius: 40px;
    color: #fff;
    background: #4c00b4;
    font-size: 20px;
    font-weight: 500;
    cursor: pointer;
}
.gray{
    background:#eaeaea;
    color: #676767;
}